.section-slider__top {
  position: relative;
}
.section-slider__title:not(:last-child) {
  margin-bottom: clamp(1.25rem, 0.875rem + 1.88vw, 3.125rem);
}
.section-slider .swiper {
  padding-top: 20px;
  padding-right: clamp(0.188rem, -0.275rem + 2.31vw, 2.5rem);
  padding-left: clamp(0.188rem, -0.275rem + 2.31vw, 2.5rem);
}
.section-slider .swiper-wrapper {
  padding-bottom: 20px;
}
.section-slider .swiper-slide {
  height: auto;
}
.section-slider .swiper-button {
  position: absolute;
  top: 43%;
  right: 0;
}